'use client';

import React from 'react';
import { MessageCircle } from 'lucide-react';

interface WhatsAppDiagnosticCTAProps {
  message: string;
  phoneNumber: string;
  buttonText?: string;
  className?: string;
  studentInfo?: {
    source: 'landing' | 'dashboard';
    timestamp: Date;
  };
  onClickTracking?: () => void;
}

export function WhatsAppDiagnosticCTA({
  message,
  phoneNumber,
  buttonText = "Commencer mon diagnostic",
  className = "",
  studentInfo,
  onClickTracking
}: WhatsAppDiagnosticCTAProps) {
  
  const handleWhatsAppClick = () => {
    // Tracking du clic
    if (onClickTracking) {
      onClickTracking();
    }

    // Analytics event
    if (typeof window !== 'undefined' && (window as any).gtag) {
      (window as any).gtag('event', 'whatsapp_diagnostic_click', {
        source: studentInfo?.source || 'unknown',
        timestamp: new Date().toISOString()
      });
    }

    // Construire l'URL WhatsApp avec le message pr√©-rempli
    const encodedMessage = encodeURIComponent(message);
    const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodedMessage}`;
    
    // Ouvrir WhatsApp dans un nouvel onglet
    window.open(whatsappUrl, '_blank', 'noopener,noreferrer');
  };

  return (
    <button
      onClick={handleWhatsAppClick}
      className={`
        inline-flex items-center gap-3 px-8 py-4 
        bg-black text-white rounded-lg font-semibold text-lg 
        hover:bg-gray-800 transition-all duration-200
        focus:ring-2 focus:ring-black focus:ring-offset-2
        ${className}
      `}
      aria-label={`${buttonText} - Ouvre WhatsApp pour commencer le diagnostic`}
    >
      <MessageCircle size={20} />
      {buttonText}
    </button>
  );
}

// Composant sp√©cialis√© pour la landing page
