'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { 
  Brain, 
  Clock, 
  Users, 
  Star, 
  BookOpen, 
  Play,
  CheckCircle,
  Sparkles
} from 'lucide-react';
import { Course } from '@/types';

interface CoursePurchaseCardProps {
  course: Course;
  isOwned?: boolean;
  packBadge?: string; // "Inclus dans Pack Sciences"
  onPurchase: (courseId: string) => void;
  onPreview?: (courseId: string) => void;
  className?: string;
}

// G√©n√©rateur de patterns coh√©rents
const generateCardPattern = (courseId: string, title: string) => {
  const hash = title.split('').reduce((a, b) => ((a << 5) - a + b.charCodeAt(0)) | 0, 0);
  const patterns = [
    'linear-gradient(135deg, rgba(59, 130, 246, 0.1) 0%, rgba(147, 51, 234, 0.1) 100%)',
    'linear-gradient(135deg, rgba(34, 197, 94, 0.1) 0%, rgba(59, 130, 246, 0.1) 100%)',
    'linear-gradient(135deg, rgba(236, 72, 153, 0.1) 0%, rgba(168, 85, 247, 0.1) 100%)',
    'linear-gradient(135deg, rgba(251, 191, 36, 0.1) 0%, rgba(34, 197, 94, 0.1) 100%)',
  ];
  return patterns[Math.abs(hash) % patterns.length];
};

const getIconColor = (courseId: string, title: string) => {
  const hash = title.split('').reduce((a, b) => ((a << 5) - a + b.charCodeAt(0)) | 0, 0);
  const colors = [
    'from-blue-500 to-purple-600',
    'from-green-500 to-blue-600',
    'from-pink-500 to-purple-600',
    'from-yellow-500 to-green-600',
  ];
  return colors[Math.abs(hash) % colors.length];
};

export function CoursePurchaseCard({ 
  course, 
  isOwned = false, 
  packBadge,
  onPurchase, 
  onPreview,
  className = ""
}: CoursePurchaseCardProps) {
  const handlePurchase = () => {
    if (!isOwned) {
      onPurchase(course.id);
    }
  };

  const handlePreview = () => {
    if (onPreview) {
      onPreview(course.id);
    }
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ y: -4, transition: { duration: 0.2 } }}
      className={`relative bg-white rounded-2xl shadow-lg overflow-hidden border border-gray-100 ${className}`}
    >
      {/* Badge Pack si applicable */}
      {packBadge && (
        <div className="absolute top-4 left-4 z-10">
          <div className="bg-gradient-to-r from-purple-500 to-pink-500 text-white px-3 py-1 rounded-full text-xs font-medium flex items-center gap-1">
            <Sparkles size={12} />
            {packBadge}
          </div>
        </div>
      )}

      {/* Badge Poss√©d√© */}
      {isOwned && (
        <div className="absolute top-4 right-4 z-10">
          <div className="bg-green-500 text-white p-2 rounded-full">
            <CheckCircle size={16} />
          </div>
        </div>
      )}

      {/* Header avec pattern */}
      <div 
        className="relative h-32 flex items-center justify-center"
        style={{ background: generateCardPattern(course.id, course.title) }}
      >
        <div className="absolute inset-0 bg-gradient-to-br from-black/5 to-black/10"></div>
        <div className={`relative w-16 h-16 rounded-2xl bg-gradient-to-br ${getIconColor(course.id, course.title)} flex items-center justify-center shadow-lg`}>
          <BookOpen className="text-white" size={24} />
        </div>
      </div>

      {/* Contenu */}
      <div className="p-6">
        {/* Titre et description */}
        <div className="mb-4">
          <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">
            {course.title}
          </h3>
          <p className="text-gray-600 text-sm line-clamp-2 mb-3">
            {course.description}
          </p>
          <div className="text-xs text-gray-500">
            Par {course.instructor}
          </div>
        </div>

        {/* M√©tadonn√©es */}
        <div className="flex items-center gap-4 mb-4 text-sm text-gray-600">
          <div className="flex items-center gap-1">
            <Clock size={14} />
            <span>{course.duration}</span>
          </div>
          <div className="flex items-center gap-1">
            <Users size={14} />
            <span>{course.totalLessons} le√ßons</span>
          </div>
          <div className="flex items-center gap-1">
            <Star size={14} />
            <span>{course.difficulty}</span>
          </div>
        </div>

        {/* Prix en cr√©dits */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-2">
            <Brain className="text-purple-600" size={20} />
            <span className="text-2xl font-bold text-gray-900">
              {course.creditCost || 20}
            </span>
            <span className="text-gray-600">cr√©dits</span>
          </div>
          {course.difficulty === 'Avanc√©' && (
            <div className="bg-orange-100 text-orange-700 px-2 py-1 rounded-full text-xs font-medium">
              Expert
            </div>
          )}
        </div>

        {/* Actions */}
        <div className="flex gap-2">
          {isOwned ? (
            <button
              onClick={handlePreview}
              className="flex-1 bg-green-50 text-green-700 py-3 px-4 rounded-xl font-medium hover:bg-green-100 transition-colors flex items-center justify-center gap-2"
            >
              <Play size={16} />
              Acc√©der au cours
            </button>
          ) : (
            <>
              {onPreview && (
                <button
                  onClick={handlePreview}
                  className="px-4 py-3 text-gray-600 hover:text-gray-800 hover:bg-gray-50 rounded-xl transition-colors"
                >
                  Aper√ßu
                </button>
              )}
              <button
                onClick={handlePurchase}
                className="flex-1 bg-gradient-to-r from-purple-600 to-blue-600 text-white py-3 px-4 rounded-xl font-medium hover:from-purple-700 hover:to-blue-700 transition-all flex items-center justify-center gap-2 shadow-lg hover:shadow-xl"
              >
                <Brain size={16} />
                D√©bloquer
              </button>
            </>
          )}
        </div>

        {/* Message d'acc√®s limit√© pour cours individuel */}
        {!packBadge && !isOwned && (
          <div className="mt-3 p-3 bg-blue-50 rounded-lg">
            <p className="text-xs text-blue-700 text-center">
              üí° <strong>Acc√®s limit√© :</strong> Ce cours uniquement
            </p>
          </div>
        )}
      </div>
    </motion.div>
  );
}
