'use client';

import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  X, 
  ArrowLeft, 
  Play, 
  CheckCircle, 
  Lock, 
  BookOpen,
  Clock,
  Target,
  Award,
  Users,
  Star,
  ChevronRight
} from 'lucide-react';
import { Course, Lesson } from '@/types';

interface EnhancedMarioMapProps {
  course: Course;
  lessons: Lesson[];
  onSelectLesson: (lesson: Lesson) => void;
  onClose: () => void;
}

interface MapNode {
  id: string;
  lesson: Lesson;
  position: { x: number; y: number };
  status: 'completed' | 'current' | 'locked' | 'available';
  connections: string[];
}

export function EnhancedMarioMap({ 
  course, 
  lessons, 
  onSelectLesson, 
  onClose 
}: EnhancedMarioMapProps) {
  const [selectedLessonId, setSelectedLessonId] = useState<string | null>(null);
  const [hoveredNodeId, setHoveredNodeId] = useState<string | null>(null);

  // Cr√©er les nodes de la map avec positions
  const mapNodes: MapNode[] = lessons.map((lesson, index) => {
    // Positions en zigzag pour cr√©er un chemin naturel
    const row = Math.floor(index / 3);
    const col = index % 3;
    const isEvenRow = row % 2 === 0;
    
    return {
      id: lesson.id,
      lesson,
      position: {
        x: isEvenRow ? col * 200 + 100 : (2 - col) * 200 + 100,
        y: row * 150 + 100
      },
      status: lesson.completed ? 'completed' : 
              lesson.isInProgress ? 'current' :
              lesson.unlocked ? 'available' : 'locked',
      connections: index < lessons.length - 1 ? [lessons[index + 1].id] : []
    };
  });

  const selectedLesson = selectedLessonId ? lessons.find(l => l.id === selectedLessonId) : null;

  const getNodeColor = (status: string) => {
    switch (status) {
      case 'completed': return 'bg-green-500 border-green-600';
      case 'current': return 'bg-blue-500 border-blue-600';
      case 'available': return 'bg-white border-gray-300';
      case 'locked': return 'bg-gray-200 border-gray-300';
      default: return 'bg-gray-200 border-gray-300';
    }
  };

  const getNodeIcon = (lesson: Lesson, status: string) => {
    if (status === 'completed') return <CheckCircle className="text-white" size={20} />;
    if (status === 'locked') return <Lock className="text-gray-500" size={20} />;
    if (lesson.type === 'video') return <Play className="text-gray-700" size={20} />;
    if (lesson.type === 'quiz') return <Target className="text-gray-700" size={20} />;
    return <BookOpen className="text-gray-700" size={20} />;
  };

  return (
    <div className="fixed inset-0 bg-white z-50 overflow-hidden">
      {/* Header */}
      <div className="bg-white border-b border-gray-200 px-6 py-4 flex items-center justify-between">
        <div className="flex items-center gap-4">
          <button
            onClick={onClose}
            className="w-10 h-10 flex items-center justify-center rounded-lg hover:bg-gray-100 transition-colors"
          >
            <ArrowLeft size={20} />
          </button>
          <div>
            <h1 className="text-xl font-bold text-gray-900">{course.title}</h1>
            <p className="text-sm text-gray-600">Votre parcours d'apprentissage</p>
          </div>
        </div>
        
        <div className="flex items-center gap-4">
          <div className="text-right">
            <div className="text-sm text-gray-600">Niveau 1</div>
            <div className="text-lg font-bold">1/5</div>
          </div>
          <div className="text-right">
            <div className="text-sm text-gray-600">Progression</div>
            <div className="text-lg font-bold text-blue-600">20%</div>
          </div>
          <div className="w-8 h-8 bg-black rounded-lg flex items-center justify-center text-white font-bold">
            +25 XP
          </div>
        </div>
      </div>

      <div className="flex h-[calc(100vh-73px)]">
        {/* Zone de la map */}
        <div className="flex-1 relative overflow-hidden">
          {/* Fond paysage */}
          <div 
            className="absolute inset-0 bg-gradient-to-b from-blue-100 via-green-50 to-green-100"
            style={{
              backgroundImage: `
                radial-gradient(circle at 20% 80%, rgba(34, 197, 94, 0.2) 0%, transparent 50%),
                radial-gradient(circle at 80% 20%, rgba(59, 130, 246, 0.2) 0%, transparent 50%),
                radial-gradient(circle at 40% 40%, rgba(168, 85, 247, 0.1) 0%, transparent 50%)
              `
            }}
          />
          
          {/* √âl√©ments d√©coratifs du paysage */}
          <div className="absolute inset-0 pointer-events-none">
            {/* Montagnes en arri√®re-plan */}
            <svg className="absolute bottom-0 left-0 w-full h-32" viewBox="0 0 1200 200" fill="none">
              <path d="M0 200 L200 100 L400 120 L600 80 L800 110 L1000 90 L1200 100 L1200 200 Z" 
                    fill="rgba(156, 163, 175, 0.3)" />
              <path d="M0 200 L150 130 L350 140 L550 110 L750 130 L950 120 L1200 130 L1200 200 Z" 
                    fill="rgba(156, 163, 175, 0.2)" />
            </svg>
            
            {/* Arbres */}
            <div className="absolute bottom-20 left-10 text-4xl">üå≤</div>
            <div className="absolute bottom-16 left-32 text-3xl">üå≥</div>
            <div className="absolute bottom-24 right-20 text-4xl">üå≤</div>
            <div className="absolute bottom-12 right-40 text-2xl">üåø</div>
            
            {/* Personnages et √©l√©ments interactifs */}
            <div className="absolute bottom-32 left-1/4 text-3xl animate-bounce">üë®‚Äçüéì</div>
            <div className="absolute bottom-28 right-1/3 text-2xl">üìö</div>
            <div className="absolute top-1/3 left-1/3 text-3xl">üèÜ</div>
            <div className="absolute bottom-40 right-1/4 text-2xl">üéØ</div>
            
            {/* Nuages */}
            <div className="absolute top-10 left-20 text-3xl opacity-60">‚òÅÔ∏è</div>
            <div className="absolute top-16 right-32 text-4xl opacity-40">‚òÅÔ∏è</div>
            
            {/* Soleil */}
            <div className="absolute top-8 right-8 text-4xl">‚òÄÔ∏è</div>
            
            {/* Ch√¢teau/Objectif final */}
            <div className="absolute top-12 right-12 text-4xl">üè∞</div>
          </div>

          {/* Rivi√®re/Chemin connectant les nodes */}
          <svg className="absolute inset-0 w-full h-full pointer-events-none">
            {mapNodes.map((node, index) => {
              if (index === mapNodes.length - 1) return null;
              const nextNode = mapNodes[index + 1];
              return (
                <g key={`path-group-${node.id}`}>
                  {/* Rivi√®re de fond (plus large, bleu clair) */}
                  <motion.path
                    d={`M ${node.position.x + 30} ${node.position.y + 30} 
                        Q ${(node.position.x + nextNode.position.x) / 2} ${node.position.y - 50} 
                        ${nextNode.position.x + 30} ${nextNode.position.y + 30}`}
                    stroke="#BFDBFE"
                    strokeWidth="12"
                    fill="none"
                    initial={{ pathLength: 0 }}
                    animate={{ pathLength: 1 }}
                    transition={{ duration: 1, delay: index * 0.2 }}
                  />
                  {/* Chemin principal (plus fin, bleu fonc√©) */}
                  <motion.path
                    d={`M ${node.position.x + 30} ${node.position.y + 30} 
                        Q ${(node.position.x + nextNode.position.x) / 2} ${node.position.y - 50} 
                        ${nextNode.position.x + 30} ${nextNode.position.y + 30}`}
                    stroke="#3B82F6"
                    strokeWidth="4"
                    fill="none"
                    initial={{ pathLength: 0 }}
                    animate={{ pathLength: 1 }}
                    transition={{ duration: 1, delay: index * 0.2 + 0.3 }}
                  />
                </g>
              );
            })}
          </svg>

          {/* Nodes des le√ßons */}
          <div className="absolute inset-0">
            {mapNodes.map((node, index) => (
              <motion.div
                key={node.id}
                className="absolute"
                style={{
                  left: node.position.x,
                  top: node.position.y
                }}
                initial={{ scale: 0, opacity: 0 }}
                animate={{ scale: 1, opacity: 1 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
                onHoverStart={() => setHoveredNodeId(node.id)}
                onHoverEnd={() => setHoveredNodeId(null)}
              >
                {/* Node principal */}
                <motion.button
                  onClick={() => {
                    setSelectedLessonId(node.id);
                    if (node.status !== 'locked') {
                      onSelectLesson(node.lesson);
                    }
                  }}
                  className={`
                    w-16 h-16 rounded-full border-4 flex items-center justify-center
                    transition-all duration-200 shadow-lg
                    ${getNodeColor(node.status)}
                    ${node.status !== 'locked' ? 'hover:scale-110 cursor-pointer' : 'cursor-not-allowed'}
                    ${selectedLessonId === node.id ? 'ring-4 ring-blue-300' : ''}
                  `}
                  whileHover={node.status !== 'locked' ? { scale: 1.1 } : {}}
                  whileTap={node.status !== 'locked' ? { scale: 0.95 } : {}}
                >
                  {getNodeIcon(node.lesson, node.status)}
                </motion.button>

                {/* Num√©ro de la le√ßon */}
                <div className="absolute -bottom-8 left-1/2 transform -translate-x-1/2">
                  <div className="bg-white px-2 py-1 rounded-full text-xs font-bold text-gray-700 shadow-sm border">
                    {index + 1}
                  </div>
                </div>

                {/* Tooltip au hover */}
                <AnimatePresence>
                  {hoveredNodeId === node.id && (
                    <motion.div
                      initial={{ opacity: 0, y: 10 }}
                      animate={{ opacity: 1, y: 0 }}
                      exit={{ opacity: 0, y: 10 }}
                      className="absolute -top-16 left-1/2 transform -translate-x-1/2 z-10"
                    >
                      <div className="bg-gray-900 text-white px-3 py-2 rounded-lg text-sm whitespace-nowrap">
                        {node.lesson.title}
                        <div className="absolute top-full left-1/2 transform -translate-x-1/2 w-0 h-0 border-l-4 border-r-4 border-t-4 border-transparent border-t-gray-900"></div>
                      </div>
                    </motion.div>
                  )}
                </AnimatePresence>
              </motion.div>
            ))}
          </div>
        </div>

        {/* Panel de d√©tails de la le√ßon */}
        <AnimatePresence>
          {selectedLesson && (
            <motion.div
              initial={{ x: 400, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              exit={{ x: 400, opacity: 0 }}
              className="w-96 bg-white border-l border-gray-200 flex flex-col"
            >
              {/* Header de la le√ßon */}
              <div className="p-6 border-b border-gray-200">
                <div className="flex items-start justify-between mb-4">
                  <div className="flex-1">
                    <h3 className="text-lg font-bold text-gray-900 mb-2">
                      {selectedLesson.title}
                    </h3>
                    <p className="text-sm text-gray-600 mb-4">
                      {selectedLesson.description}
                    </p>
                  </div>
                  <button
                    onClick={() => setSelectedLessonId(null)}
                    className="w-8 h-8 flex items-center justify-center rounded-lg hover:bg-gray-100"
                  >
                    <X size={16} />
                  </button>
                </div>

                {/* Tags de difficult√© et type */}
                <div className="flex gap-2 mb-4">
                  <span className="px-2 py-1 bg-blue-100 text-blue-700 text-xs rounded-full">
                    En cours
                  </span>
                  <span className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded-full">
                    Interm√©diaire
                  </span>
                  <span className="px-2 py-1 bg-purple-100 text-purple-700 text-xs rounded-full">
                    24% termin√©
                  </span>
                </div>

                {/* Barre de progression */}
                <div className="mb-4">
                  <div className="flex justify-between text-sm text-gray-600 mb-2">
                    <span>Progression</span>
                    <span>24%</span>
                  </div>
                  <div className="h-2 bg-gray-200 rounded-full overflow-hidden">
                    <motion.div
                      initial={{ width: 0 }}
                      animate={{ width: '24%' }}
                      transition={{ duration: 1, ease: "easeOut" }}
                      className="h-full bg-gradient-to-r from-blue-500 to-purple-500 rounded-full"
                    />
                  </div>
                </div>
              </div>

              {/* Contenu de la le√ßon */}
              <div className="flex-1 p-6 overflow-y-auto">
                {/* Ce que vous allez apprendre */}
                <div className="mb-6">
                  <h4 className="font-semibold text-gray-900 mb-3 flex items-center gap-2">
                    <Target size={16} />
                    Ce que vous allez apprendre
                  </h4>
                  <ul className="space-y-2">
                    {selectedLesson.objectives?.map((objective, index) => (
                      <li key={index} className="flex items-start gap-2 text-sm text-gray-600">
                        <CheckCircle size={14} className="text-green-500 mt-0.5 flex-shrink-0" />
                        {objective}
                      </li>
                    )) || [
                      "Ma√Ætriser les techniques avanc√©es du domaine",
                      "Comprendre les concepts fondamentaux",
                      "D√©velopper une approche m√©thodique",
                      "R√©soudre des probl√®mes complexes"
                    ].map((objective, index) => (
                      <li key={index} className="flex items-start gap-2 text-sm text-gray-600">
                        <CheckCircle size={14} className="text-green-500 mt-0.5 flex-shrink-0" />
                        {objective}
                      </li>
                    ))}
                  </ul>
                </div>

                {/* Informations d√©taill√©es */}
                <div className="grid grid-cols-2 gap-4 mb-6">
                  <div className="text-center p-3 bg-gray-50 rounded-lg">
                    <Clock className="mx-auto text-gray-600 mb-1" size={16} />
                    <div className="text-sm font-medium text-gray-900">22 min</div>
                    <div className="text-xs text-gray-500">Dur√©e</div>
                  </div>
                  <div className="text-center p-3 bg-gray-50 rounded-lg">
                    <Award className="mx-auto text-gray-600 mb-1" size={16} />
                    <div className="text-sm font-medium text-gray-900">+15 XP</div>
                    <div className="text-xs text-gray-500">R√©compense</div>
                  </div>
                  <div className="text-center p-3 bg-gray-50 rounded-lg">
                    <Users className="mx-auto text-gray-600 mb-1" size={16} />
                    <div className="text-sm font-medium text-gray-900">Interm√©diaire</div>
                    <div className="text-xs text-gray-500">Niveau</div>
                  </div>
                  <div className="text-center p-3 bg-gray-50 rounded-lg">
                    <Star className="mx-auto text-gray-600 mb-1" size={16} />
                    <div className="text-sm font-medium text-gray-900">Vid√©o</div>
                    <div className="text-xs text-gray-500">Type</div>
                  </div>
                </div>

                {/* Pr√©requis recommand√©s */}
                <div className="mb-6">
                  <h4 className="font-semibold text-gray-900 mb-3">Pr√©requis recommand√©s</h4>
                  <ul className="space-y-2">
                    <li className="flex items-center gap-2 text-sm text-gray-600">
                      <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                      Avoir termin√© "Les fondamentaux"
                    </li>
                    <li className="flex items-center gap-2 text-sm text-gray-600">
                      <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                      Comprendre les bases du sujet
                    </li>
                  </ul>
                </div>
              </div>

              {/* Actions */}
              <div className="p-6 border-t border-gray-200">
                <button
                  onClick={() => onSelectLesson(selectedLesson)}
                  className="w-full bg-black text-white py-3 rounded-lg font-medium hover:bg-gray-800 transition-colors flex items-center justify-center gap-2"
                >
                  <Play size={18} />
                  Continuer
                </button>
              </div>
            </motion.div>
          )}
        </AnimatePresence>
      </div>
    </div>
  );
}