'use client';

import React, { useState } from 'react';
import { motion } from 'framer-motion';
import { 
  Play, 
  Clock, 
  Star, 
  Users, 
  TrendingUp, 
  Lock, 
  GripVertical, 
  BookOpen,
  CheckCircle,
  Target,
  Zap,
  Award,
  Eye,
  Heart,
  Brain,
  Layers3
} from 'lucide-react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { Course, StudentProgress } from '@/types';
import { cn, formatDuration, getProgressColor, getMotivationMessage } from '@/lib/utils';

interface CourseCardProps {
  course: Course;
  progress?: StudentProgress;
  isDraggable?: boolean;
  onToggleFavorite?: (courseId: string) => void;
  onPreview?: (courseId: string) => void;
  onEnroll?: (courseId: string) => void;
  onOpenCourse?: (course: Course) => void;
  onOpenStaircaseView?: (course: Course) => void;
  userCredits?: number;
  canAfford?: boolean;
  isUnlocked?: boolean;
}

// üé® G√©n√©rateur de patterns visuels bas√© sur le contenu du cours
const generateCoursePattern = (courseId: string, title: string) => {
  const hash = title.split('').reduce((a, b) => ((a << 5) - a + b.charCodeAt(0)) | 0, 0);
  const patterns = [
    'radial-gradient(circle at 20% 30%, rgba(59, 130, 246, 0.3) 0%, transparent 50%), radial-gradient(circle at 80% 70%, rgba(147, 51, 234, 0.3) 0%, transparent 50%)',
    'linear-gradient(135deg, rgba(34, 197, 94, 0.2) 0%, rgba(59, 130, 246, 0.2) 100%), radial-gradient(circle at 60% 40%, rgba(251, 191, 36, 0.2) 0%, transparent 70%)',
    'conic-gradient(from 180deg at 50% 50%, rgba(236, 72, 153, 0.2) 0deg, rgba(59, 130, 246, 0.2) 120deg, rgba(34, 197, 94, 0.2) 240deg, rgba(236, 72, 153, 0.2) 360deg)',
    'radial-gradient(ellipse at 30% 20%, rgba(168, 85, 247, 0.3) 0%, transparent 60%), linear-gradient(45deg, rgba(34, 197, 94, 0.1) 0%, rgba(59, 130, 246, 0.1) 100%)',
    'linear-gradient(45deg, rgba(239, 68, 68, 0.2) 0%, rgba(251, 191, 36, 0.2) 50%, rgba(34, 197, 94, 0.2) 100%)'
  ];
  return patterns[Math.abs(hash) % patterns.length];
};

// üéØ S√©lection d'ic√¥ne contextuelle
const getCourseIcon = (title: string, faculty: string) => {
  if (title.toLowerCase().includes('math')) return Brain;
  if (title.toLowerCase().includes('physique')) return Zap;
  if (title.toLowerCase().includes('√©conomie') || faculty.includes('Solvay')) return TrendingUp;
  if (title.toLowerCase().includes('informatique')) return Target;
  return BookOpen;
};

// üåà Couleurs adapt√©es par domaine
const getDomainColors = (title: string, faculty: string) => {
  if (title.toLowerCase().includes('math')) return {
    primary: 'from-blue-500 to-indigo-600',
    accent: 'bg-blue-100 text-blue-700',
    hover: 'group-hover:text-blue-600'
  };
  if (title.toLowerCase().includes('physique')) return {
    primary: 'from-purple-500 to-violet-600', 
    accent: 'bg-purple-100 text-purple-700',
    hover: 'group-hover:text-purple-600'
  };
  if (title.toLowerCase().includes('√©conomie') || faculty.includes('Solvay')) return {
    primary: 'from-emerald-500 to-teal-600',
    accent: 'bg-emerald-100 text-emerald-700', 
    hover: 'group-hover:text-emerald-600'
  };
  if (title.toLowerCase().includes('informatique')) return {
    primary: 'from-orange-500 to-amber-600',
    accent: 'bg-orange-100 text-orange-700',
    hover: 'group-hover:text-orange-600'
  };
  return {
    primary: 'from-gray-500 to-slate-600',
    accent: 'bg-gray-100 text-gray-700',
    hover: 'group-hover:text-gray-600'
  };
};

export function CourseCard({ 
  course, 
  progress, 
  isDraggable = false,
  onToggleFavorite,
  onPreview,
  onEnroll,
  onOpenCourse,
  onOpenStaircaseView,
  userCredits = 0,
  canAfford = true,
  isUnlocked = false
}: CourseCardProps) {
  const [isHovered, setIsHovered] = useState(false);
  const [isAnimating, setIsAnimating] = useState(false);
  
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({
    id: course.id,
    disabled: !isDraggable,
  });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  // üé® G√©n√©ration du design adaptatif
  const pattern = generateCoursePattern(course.id, course.title);
  const IconComponent = getCourseIcon(course.title, course.faculty);
  const colors = getDomainColors(course.title, course.faculty);
  const progressColor = getProgressColor(course.progress);
  const motivationMessage = progress ? getMotivationMessage(course.progress, progress.facultyAverage) : null;

  // ü§ù Messages bienveillants selon le statut
  const getEncouragingMessage = () => {
    if (course.isOwned) {
      if (course.progress === 100) return "üéâ F√©licitations ! Cours ma√Ætris√©";
      if (course.progress >= 80) return "üí™ Presque termin√© ! Vous y √™tes";
      if (course.progress >= 50) return "üöÄ Excellent progr√®s ! Continuez";
      if (course.progress >= 20) return "‚ú® Bon d√©but ! On continue ensemble";
      return "üå± Votre parcours commence ici";
    }
    return "üëã D√©couvrir ce cours";
  };

  // üìä Calcul du niveau de confiance
  const getTrustLevel = () => {
    const factors = [
      course.instructor ? 1 : 0,
      course.totalLessons >= 5 ? 1 : 0,
      course.rating >= 4 ? 1 : 0,
      course.enrolledStudents >= 100 ? 1 : 0
    ];
    return factors.reduce((a, b) => a + b, 0);
  };

  return (
    <motion.div
      ref={setNodeRef}
      style={style}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ 
        y: -12,
        scale: 1.02,
        transition: { type: "spring", stiffness: 300, damping: 20 }
      }}
      transition={{ duration: 0.4 }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={() => {
        setIsAnimating(true);
        setTimeout(() => setIsAnimating(false), 600);
        onOpenCourse?.(course);
      }}
      // üîë Accessibilit√© Web Standards
      role="article"
      aria-label={`Cours: ${course.title} par ${course.instructor}`}
      aria-describedby={`course-${course.id}-description`}
      tabIndex={0}
      onKeyDown={(e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          onOpenCourse?.(course);
        }
      }}
      className={cn(
        "group relative overflow-hidden bg-white/80 backdrop-blur-sm rounded-3xl shadow-lg hover:shadow-2xl cursor-pointer transition-all duration-500 border border-white/20",
        "focus:outline-none focus:ring-4 focus:ring-blue-500/20 focus:border-blue-500/40",
        isDragging && "opacity-50 scale-95 rotate-1",
        isAnimating && "animate-pulse",
        "hover:border-white/40"
      )}
    >
      {/* üé® Header avec Pattern G√©n√©ratif (remplace les thumbnails) */}
      <div className="relative h-52 overflow-hidden">
        {/* Pattern g√©n√©ratif unique */}
        <div 
          className="w-full h-full relative"
          style={{
            background: `linear-gradient(135deg, rgba(255,255,255,0.1) 0%, rgba(255,255,255,0.05) 100%), ${pattern}`,
          }}
        >
          {/* Mesh gradient overlay pour la profondeur */}
          <div className="absolute inset-0 bg-gradient-to-br from-white/10 via-transparent to-black/5" />
          
          {/* Ic√¥ne contextuelle centrale */}
          <div className="absolute inset-0 flex items-center justify-center">
            <motion.div
              animate={isHovered ? { scale: 1.1, rotate: 5 } : { scale: 1, rotate: 0 }}
              transition={{ type: "spring", stiffness: 200, damping: 15 }}
              className={`p-6 rounded-2xl bg-gradient-to-br ${colors.primary} shadow-lg backdrop-blur-sm`}
            >
              <IconComponent size={48} className="text-white drop-shadow-lg" />
            </motion.div>
          </div>

          {/* Particules flottantes pour effet Web 3.0 */}
          <motion.div 
            className="absolute top-4 left-4 w-2 h-2 bg-white/60 rounded-full"
            animate={{ y: [0, -10, 0], opacity: [0.6, 1, 0.6] }}
            transition={{ duration: 3, repeat: Infinity, delay: 0 }}
          />
          <motion.div 
            className="absolute top-8 right-6 w-1.5 h-1.5 bg-white/40 rounded-full"
            animate={{ y: [0, -8, 0], opacity: [0.4, 0.8, 0.4] }}
            transition={{ duration: 2.5, repeat: Infinity, delay: 1 }}
          />
          <motion.div 
            className="absolute bottom-6 left-8 w-1 h-1 bg-white/50 rounded-full"
            animate={{ y: [0, -6, 0], opacity: [0.5, 0.9, 0.5] }}
            transition={{ duration: 2, repeat: Infinity, delay: 0.5 }}
          />
        </div>

        {/* Drag Handle - Design am√©lior√© */}
        {isDraggable && (
          <motion.div
            {...attributes}
            {...listeners}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            className="absolute top-4 right-4 p-2.5 bg-white/20 backdrop-blur-md rounded-xl text-white hover:bg-white/30 cursor-grab active:cursor-grabbing z-10 border border-white/10"
          >
            <GripVertical size={16} />
          </motion.div>
        )}

        {/* Badge de confiance (remplace "populaire") */}
        <div className="absolute top-4 left-4">
          <motion.div 
            initial={{ opacity: 0, x: -20 }}
            animate={{ opacity: 1, x: 0 }}
            transition={{ delay: 0.2 }}
            className={`inline-flex items-center gap-2 px-3 py-1.5 ${colors.accent} backdrop-blur-sm text-xs font-semibold rounded-full border border-white/20`}
          >
            {getTrustLevel() >= 3 && <Award size={12} />}
            {getTrustLevel() >= 3 ? 'Excellence' : getTrustLevel() >= 2 ? 'Qualit√©' : 'Nouveau'}
          </motion.div>
        </div>

        {/* Bouton favori - Design bienveillant */}
        <motion.button
          onClick={(e) => {
            e.stopPropagation();
            onToggleFavorite?.(course.id);
          }}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          aria-label={course.isPrimary ? 'Retirer des favoris' : 'Ajouter aux favoris'}
          className="absolute bottom-4 right-4 p-3 bg-white/90 backdrop-blur-sm rounded-full text-gray-700 hover:text-red-500 hover:bg-white shadow-lg transition-all duration-200 border border-white/20 focus:outline-none focus:ring-2 focus:ring-red-500/30"
        >
          <Heart 
            size={18} 
            fill={course.isPrimary ? 'currentColor' : 'none'} 
            className={course.isPrimary ? 'text-red-500' : ''} 
          />
        </motion.button>
      </div>

      {/* üìö Contenu Principal - Approche Bienveillante */}
      <div className="p-6 space-y-4">
        {/* En-t√™te avec titre et message encourageant */}
        <div className="space-y-3">
          <div className="flex items-start justify-between gap-3">
            <h3 className={`text-lg font-bold text-gray-900 line-clamp-2 transition-colors ${colors.hover}`}>
              {course.title}
            </h3>
            {course.isOwned && course.progress === 100 && (
              <CheckCircle size={20} className="text-emerald-500 flex-shrink-0" />
            )}
          </div>
          
          {/* Message encourageant */}
          <motion.div 
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 0.3 }}
            id={`course-${course.id}-description`}
            className="text-sm text-gray-600 font-medium bg-gray-50 px-3 py-2 rounded-lg border border-gray-100"
          >
            {getEncouragingMessage()}
          </motion.div>

          {/* Instructeur avec badge de confiance */}
          <div className="flex items-center gap-2 text-sm text-gray-500">
            <div className="flex items-center gap-1">
              <div className="w-6 h-6 bg-gray-200 rounded-full flex items-center justify-center">
                <span className="text-xs font-semibold">{course.instructor?.charAt(0) || 'P'}</span>
              </div>
              <span className="font-medium">{course.instructor}</span>
            </div>
          </div>
        </div>

        {/* Section progression - Design am√©lior√© */}
        {course.isOwned && (
          <motion.div 
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.4 }}
            className="space-y-3"
          >
            <div className="flex justify-between items-center">
              <span className="text-sm font-semibold text-gray-700">Votre progression</span>
              <div className="flex items-center gap-2">
                <span className={`text-sm font-bold ${colors.hover.replace('group-hover:', '')}`}>
                  {course.progress}%
                </span>
                {course.progress >= 75 && <Zap size={14} className="text-amber-500" />}
              </div>
            </div>
            
            <div className="relative">
              <div className="w-full bg-gray-200 rounded-full h-3 overflow-hidden">
                <motion.div 
                  className={`h-full bg-gradient-to-r ${colors.primary} rounded-full relative`}
                  initial={{ width: 0 }}
                  animate={{ width: `${course.progress}%` }}
                  transition={{ duration: 1.5, delay: 0.6, ease: "easeOut" }}
                >
                  {/* Effet de brillance */}
                  <motion.div
                    className="absolute inset-0 bg-gradient-to-r from-transparent via-white/30 to-transparent"
                    animate={{ x: ['-100%', '100%'] }}
                    transition={{ duration: 2, repeat: Infinity, repeatDelay: 3 }}
                  />
                </motion.div>
              </div>
            </div>
            
            <div className="flex justify-between items-center text-xs text-gray-600">
              <span className="font-medium">{course.completedLessons}/{course.totalLessons} le√ßons</span>
              {progress && (
                <span className="font-semibold px-2 py-1 bg-blue-50 text-blue-700 rounded-full">
                  Top {Math.round((progress.facultyRanking / progress.totalStudents) * 100)}%
                </span>
              )}
            </div>
          </motion.div>
        )}

        {/* Footer avec m√©triques de confiance */}
        <div className="flex items-center justify-between pt-4 border-t border-gray-100">
          {/* Indicateurs de qualit√© */}
          <div className="flex items-center gap-3">
            <div className="flex items-center gap-1">
              {[1, 2, 3, 4, 5].map((star) => (
                <Star 
                  key={star} 
                  size={14} 
                  className="text-amber-400" 
                  fill={star <= (course.rating || 4) ? 'currentColor' : 'none'} 
                />
              ))}
              <span className="text-xs text-gray-600 ml-1 font-medium">
                {course.rating || 4.2}/5
              </span>
            </div>
          </div>
          
          {/* M√©tadonn√©es */}
          <div className="flex items-center gap-3 text-xs text-gray-500">
            <div className="flex items-center gap-1">
              <Clock size={14} />
              <span className="font-medium">{course.duration}</span>
            </div>
            {course.enrolledStudents && (
              <div className="flex items-center gap-1">
                <Users size={14} />
                <span className="font-medium">{course.enrolledStudents}+</span>
              </div>
            )}
          </div>
        </div>

        {/* Prix en cr√©dits cognitifs */}
        {!course.isOwned && !isUnlocked && course.creditCost && (
          <div className="flex items-center justify-center gap-2 py-2 px-3 bg-gradient-to-r from-purple-50 to-blue-50 rounded-xl border border-purple-200">
            <span className="text-2xl">üß†</span>
            <span className="text-lg font-bold text-gray-900">{course.creditCost}</span>
            <span className="text-sm text-gray-600">cr√©dits</span>
          </div>
        )}

        {/* Badge d√©bloqu√© */}
        {isUnlocked && (
          <div className="flex items-center justify-center gap-2 py-2 px-3 bg-green-50 rounded-xl border border-green-200">
            <CheckCircle className="text-green-600" size={16} />
            <span className="text-sm font-medium text-green-700">Cours d√©bloqu√©</span>
          </div>
        )}

        {/* Actions - Design bienveillant */}
        <div className="pt-2 space-y-2">
          {course.isOwned || isUnlocked ? (
            <div className="flex gap-2">
              <motion.button
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                aria-label={`${course.progress === 100 ? 'R√©viser' : course.progress > 0 ? 'Continuer' : 'Commencer'} le cours ${course.title}`}
                className={`flex-1 py-3 px-4 bg-gradient-to-r ${colors.primary} text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-200 flex items-center justify-center gap-2 focus:outline-none focus:ring-4 focus:ring-blue-500/20`}
              >
                <Play size={16} />
                {course.progress === 100 ? 'R√©viser' : course.progress > 0 ? 'Continuer' : 'Commencer'}
              </motion.button>
              
              {onOpenStaircaseView && (
                <motion.button
                  onClick={(e) => {
                    e.stopPropagation();
                    onOpenStaircaseView(course);
                  }}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  aria-label={`Vue escalier 3D du cours ${course.title}`}
                  className="py-3 px-4 bg-gradient-to-r from-purple-500 to-indigo-500 text-white font-semibold rounded-xl shadow-lg hover:shadow-xl transition-all duration-200 flex items-center justify-center focus:outline-none focus:ring-4 focus:ring-purple-500/20"
                  title="Vue escalier 3D - Parcours visuel des le√ßons"
                >
                  <Layers3 size={16} />
                </motion.button>
              )}
            </div>
          ) : (
            <div className="flex gap-2">
              <motion.button
                onClick={(e) => {
                  e.stopPropagation();
                  onPreview?.(course.id);
                }}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                aria-label={`Aper√ßu gratuit du cours ${course.title}`}
                className="flex-1 py-3 px-4 bg-gray-100 hover:bg-gray-200 text-gray-700 font-semibold rounded-xl transition-all duration-200 flex items-center justify-center gap-2 focus:outline-none focus:ring-4 focus:ring-gray-300/50"
              >
                <Eye size={16} />
                Aper√ßu
              </motion.button>
              <motion.button
                onClick={(e) => {
                  e.stopPropagation();
                  onEnroll?.(course.id);
                }}
                whileHover={{ scale: canAfford ? 1.02 : 1 }}
                whileTap={{ scale: canAfford ? 0.98 : 1 }}
                disabled={!canAfford}
                aria-label={`D√©bloquer le cours ${course.title} avec ${course.creditCost} cr√©dits`}
                className={`flex-1 py-3 px-4 font-semibold rounded-xl shadow-lg transition-all duration-200 flex items-center justify-center gap-2 focus:outline-none focus:ring-4 ${
                  canAfford 
                    ? `bg-gradient-to-r ${colors.primary} text-white hover:shadow-xl focus:ring-blue-500/20`
                    : 'bg-gray-300 text-gray-500 cursor-not-allowed focus:ring-gray-300/20'
                }`}
              >
                <Brain size={16} />
                {canAfford 
                  ? `D√©bloquer (${course.creditCost || 1} üß†)` 
                  : 'Cr√©dits insuffisants'
                }
              </motion.button>
            </div>
          )}
        </div>
      </div>
    </motion.div>
  );
}
