'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { 
  Brain, 
  Clock, 
  Users, 
  Star, 
  BookOpen, 
  Package,
  CheckCircle,
  Sparkles,
  Crown,
  Zap,
  Trophy,
  Target,
  Gift,
  ArrowRight
} from 'lucide-react';
import { CoursePack, Course } from '@/types';

interface CoursePackCardProps {
  pack: CoursePack;
  courses: Course[]; // Cours inclus dans le pack
  isOwned?: boolean;
  onPurchase: (packId: string) => void;
  onPreview?: (packId: string) => void;
  className?: string;
}

// Ic√¥nes pour les diff√©rents types de packs
const getPackIcon = (packId: string) => {
  if (packId.includes('excellence') || packId.includes('expert')) return Crown;
  if (packId.includes('rentree') || packId.includes('nouveau')) return Sparkles;
  if (packId.includes('premium') || packId.includes('ultime')) return Trophy;
  return Package;
};

// Couleurs pour les badges
const getBadgeColor = (badge: string) => {
  switch (badge?.toLowerCase()) {
    case 'populaire': return 'bg-gradient-to-r from-green-500 to-emerald-500';
    case 'recommand√©': return 'bg-gradient-to-r from-blue-500 to-indigo-500';
    case 'nouveau': return 'bg-gradient-to-r from-purple-500 to-pink-500';
    case 'premium': return 'bg-gradient-to-r from-yellow-500 to-orange-500';
    default: return 'bg-gradient-to-r from-gray-500 to-gray-600';
  }
};

export function CoursePackCard({ 
  pack, 
  courses,
  isOwned = false, 
  onPurchase, 
  onPreview,
  className = ""
}: CoursePackCardProps) {
  const PackIcon = getPackIcon(pack.id);
  const totalDuration = courses.reduce((acc, course) => {
    const duration = parseInt(course.duration?.replace(/[^\d]/g, '') || '0');
    return acc + duration;
  }, 0);
  
  const totalLessons = courses.reduce((acc, course) => acc + (course.totalLessons || 0), 0);

  const handlePurchase = () => {
    if (!isOwned) {
      onPurchase(pack.id);
    }
  };

  const handlePreview = () => {
    if (onPreview) {
      onPreview(pack.id);
    }
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ y: -6, transition: { duration: 0.2 } }}
      className={`relative bg-white rounded-3xl shadow-xl overflow-hidden border-2 border-gray-100 ${className}`}
    >
      {/* Badge Premium */}
      {pack.badge && (
        <div className="absolute top-6 right-6 z-10">
          <div className={`${getBadgeColor(pack.badge)} text-white px-4 py-2 rounded-full text-sm font-bold flex items-center gap-2 shadow-lg`}>
            <Star size={14} />
            {pack.badge}
          </div>
        </div>
      )}

      {/* Badge Poss√©d√© */}
      {isOwned && (
        <div className="absolute top-6 left-6 z-10">
          <div className="bg-green-500 text-white p-3 rounded-full shadow-lg">
            <CheckCircle size={20} />
          </div>
        </div>
      )}

      {/* Header avec gradient */}
      <div className={`relative h-40 bg-gradient-to-br ${pack.color} flex items-center justify-center`}>
        <div className="absolute inset-0 bg-gradient-to-br from-black/10 to-black/20"></div>
        <div className="relative z-10 text-center text-white">
          <div className="w-20 h-20 mx-auto mb-4 bg-white/20 backdrop-blur-sm rounded-2xl flex items-center justify-center">
            <div className="text-4xl">{pack.icon}</div>
          </div>
          <h3 className="text-2xl font-bold mb-2">{pack.title}</h3>
        </div>
      </div>

      {/* Contenu principal */}
      <div className="p-8">
        {/* Description */}
        <p className="text-gray-600 mb-6 leading-relaxed">
          {pack.description}
        </p>

        {/* M√©tadonn√©es du pack */}
        <div className="flex items-center gap-6 mb-6 text-sm text-gray-600">
          <div className="flex items-center gap-2">
            <BookOpen size={16} />
            <span>{courses.length} cours</span>
          </div>
          <div className="flex items-center gap-2">
            <Users size={16} />
            <span>{totalLessons} le√ßons</span>
          </div>
          <div className="flex items-center gap-2">
            <Clock size={16} />
            <span>{totalDuration}h+</span>
          </div>
        </div>

        {/* Prix en cr√©dits avec √©conomies */}
        <div className="bg-gradient-to-r from-purple-50 to-blue-50 rounded-2xl p-6 mb-6">
          <div className="flex items-center justify-between mb-3">
            <div className="flex items-center gap-3">
              <Brain className="text-purple-600" size={24} />
              <div>
                <div className="flex items-center gap-2">
                  <span className="text-3xl font-bold text-gray-900">
                    {pack.creditCost}
                  </span>
                  <span className="text-gray-600">cr√©dits</span>
                </div>
                {pack.savings > 0 && (
                  <div className="text-sm text-green-600 font-medium">
                    √âconomise {pack.savings} cr√©dits
                  </div>
                )}
              </div>
            </div>
            <div className="text-right">
              <div className="text-sm text-gray-500 line-through">
                {pack.originalCreditCost} cr√©dits
              </div>
              <div className="text-sm text-green-600 font-medium">
                Valeur ajout√©e
              </div>
            </div>
          </div>
        </div>

        {/* Cours inclus */}
        <div className="mb-6">
          <h4 className="font-bold text-gray-900 mb-4 flex items-center gap-2">
            <Gift className="text-purple-600" size={18} />
            Ce pack inclut :
          </h4>
          <div className="space-y-3">
            {courses.map((course, index) => (
              <motion.div
                key={course.id}
                initial={{ opacity: 0, x: -20 }}
                animate={{ opacity: 1, x: 0 }}
                transition={{ delay: index * 0.1 }}
                className="flex items-center gap-3 p-3 bg-gray-50 rounded-xl"
              >
                <div className="w-8 h-8 bg-gradient-to-br from-blue-500 to-purple-600 rounded-lg flex items-center justify-center">
                  <BookOpen className="text-white" size={14} />
                </div>
                <div className="flex-1">
                  <div className="font-medium text-gray-900 text-sm">{course.title}</div>
                  <div className="text-xs text-gray-500">{course.duration} ‚Ä¢ {course.totalLessons} le√ßons</div>
                </div>
                <div className="text-xs text-purple-600 font-medium">
                  {course.creditCost || 20} üß†
                </div>
              </motion.div>
            ))}
          </div>
        </div>

        {/* Fonctionnalit√©s bonus */}
        <div className="mb-6">
          <h4 className="font-bold text-gray-900 mb-3 flex items-center gap-2">
            <Zap className="text-yellow-500" size={18} />
            Bonus inclus :
          </h4>
          <div className="grid grid-cols-1 gap-2">
            {pack.features.map((feature, index) => (
              <div key={index} className="flex items-center gap-2 text-sm text-gray-600">
                <div className="w-1.5 h-1.5 bg-purple-500 rounded-full"></div>
                <span>{feature}</span>
              </div>
            ))}
          </div>
        </div>

        {/* Actions */}
        <div className="flex gap-3">
          {isOwned ? (
            <button
              onClick={handlePreview}
              className="flex-1 bg-green-50 text-green-700 py-4 px-6 rounded-2xl font-bold hover:bg-green-100 transition-colors flex items-center justify-center gap-2"
            >
              <CheckCircle size={18} />
              Pack activ√©
            </button>
          ) : (
            <>
              {onPreview && (
                <button
                  onClick={handlePreview}
                  className="px-6 py-4 text-gray-600 hover:text-gray-800 hover:bg-gray-50 rounded-2xl transition-colors font-medium"
                >
                  Aper√ßu
                </button>
              )}
              <button
                onClick={handlePurchase}
                className={`flex-1 bg-gradient-to-r ${pack.color} text-white py-4 px-6 rounded-2xl font-bold hover:shadow-2xl transition-all flex items-center justify-center gap-2 shadow-lg`}
              >
                <Brain size={18} />
                <span>D√©bloquer le pack</span>
                <ArrowRight size={16} />
              </button>
            </>
          )}
        </div>

        {/* Message de valeur ajout√©e */}
        {!isOwned && (
          <div className="mt-4 p-4 bg-gradient-to-r from-purple-50 to-blue-50 rounded-2xl border border-purple-100">
            <div className="flex items-start gap-3">
              <Target className="text-purple-600 mt-0.5" size={18} />
              <div>
                <p className="text-sm font-medium text-purple-900 mb-1">
                  üöÄ <strong>Acc√®s √©tendu :</strong> Plusieurs cours + accompagnement complet
                </p>
                <p className="text-xs text-purple-700">
                  Avec ce pack, tu d√©bloques plus de contenu et un accompagnement plus complet.
                </p>
              </div>
            </div>
          </div>
        )}
      </div>
    </motion.div>
  );
}
