'use client';

import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Play, 
  Eye, 
  Brain, 
  Clock, 
  Users, 
  CheckCircle,
  Star,
  Zap,
  BookOpen,
  ArrowRight
} from 'lucide-react';
import { Course, MiniQuizQuestion } from '@/types';
import { MiniQuiz } from './MiniQuiz';
import { getMiniQuizForCourse } from '@/lib/mock-data';

interface SuggestedCourseCardProps {
  course: Course;
  enrolledStudents: number;
  reason: string;
  onUnlock: (courseId: string) => void;
  onPreview: (courseId: string) => void;
  canAfford: boolean;
  isUnlocked: boolean;
}

export function SuggestedCourseCard({ 
  course, 
  enrolledStudents, 
  reason, 
  onUnlock, 
  onPreview, 
  canAfford, 
  isUnlocked 
}: SuggestedCourseCardProps) {
  const [showMiniQuiz, setShowMiniQuiz] = useState(false);
  const [quizCompleted, setQuizCompleted] = useState(false);
  const [quizScore, setQuizScore] = useState<{ score: number; total: number } | null>(null);

  const miniQuizQuestions = getMiniQuizForCourse(course.id);
  const hasMiniQuiz = miniQuizQuestions.length > 0;


  const handleQuizComplete = (score: number, totalQuestions: number) => {
    setQuizScore({ score, total: totalQuestions });
    setQuizCompleted(true);
    setTimeout(() => {
      setShowMiniQuiz(false);
    }, 3000);
  };

  const getReasonDisplay = (reason: string) => {
    const translations = {
      'Popular in your faculty': 'Populaire dans votre facult√©',
      'High success rate': 'Taux de r√©ussite √©lev√©',
      'Recommended by similar students': 'Recommand√© par des √©tudiants similaires',
      'Trending this week': 'Tendance cette semaine',
      'Perfect for your level': 'Parfait pour votre niveau'
    };
    return translations[reason] || reason;
  };

  const reasonDisplay = getReasonDisplay(reason);

  const colors = {
    primary: 'from-blue-500 to-purple-600',
    secondary: 'from-gray-400 to-gray-600'
  };

  if (showMiniQuiz) {
    return (
      <AnimatePresence>
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: 20 }}
          transition={{ duration: 0.4, ease: [0.25, 0.46, 0.45, 0.94] }}
          className="fixed inset-0 bg-gradient-to-br from-slate-50/80 via-white/90 to-blue-50/80 backdrop-blur-sm flex items-center justify-center z-50 p-4"
        >
          <div className="bg-white/95 backdrop-blur-xl rounded-2xl max-w-5xl w-full max-h-[95vh] overflow-hidden shadow-2xl border border-white/20 ring-1 ring-black/5">
            {/* Header moderne avec navigation claire */}
            <div className="bg-gradient-to-r from-slate-50 to-blue-50 px-8 py-6 border-b border-gray-100/50">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-4">
                  <div className="w-12 h-12 bg-gradient-to-br from-blue-500 to-indigo-600 rounded-xl flex items-center justify-center shadow-lg">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" className="text-white">
                      <path d="M9 12l2 2 4-4M21 12a9 9 0 11-18 0 9 9 0 0118 0z" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                    </svg>
                  </div>
                  <div>
                    <h2 className="text-xl font-semibold text-gray-900 tracking-tight">Test de Niveau</h2>
                    <p className="text-sm text-gray-600 font-medium">√âvaluez vos connaissances ‚Ä¢ {course.title}</p>
                  </div>
                </div>
                <motion.button
                  onClick={() => setShowMiniQuiz(false)}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  className="w-10 h-10 bg-white/80 hover:bg-white rounded-xl flex items-center justify-center text-gray-400 hover:text-gray-600 transition-all duration-200 shadow-sm border border-gray-200/50"
                >
                  <svg width="18" height="18" viewBox="0 0 24 24" fill="none">
                    <path d="M18 6L6 18M6 6L18 18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
                  </svg>
                </motion.button>
              </div>
            </div>
            
            {/* Contenu avec scroll fluide */}
            <div className="overflow-y-auto max-h-[calc(95vh-120px)] scrollbar-thin scrollbar-thumb-gray-300 scrollbar-track-transparent">
              <div className="p-8">
                <MiniQuiz
                  questions={miniQuizQuestions}
                  onComplete={handleQuizComplete}
                  onClose={() => {
                    setShowMiniQuiz(false);
                    if (quizCompleted) {
                      onUnlock(course);
                    }
                  }}
                  courseTitle={course.title}
                />
              </div>
            </div>
          </div>
        </motion.div>
      </AnimatePresence>
    );
  }

  return (
    <motion.div
      whileHover={{ y: -2 }}
      className="bg-white rounded-2xl border border-gray-200 hover:border-gray-300 hover:shadow-lg transition-all duration-200 overflow-hidden"
    >
      {/* Badge de recommandation */}
      <div className="relative">
        <div className="absolute top-4 left-4 z-10">
          <div className="px-3 py-1 bg-blue-100 text-blue-700 text-xs font-medium rounded-full flex items-center gap-1">
            <Star size={12} />
            {reasonDisplay}
          </div>
        </div>
        
        {/* Image du cours */}
        <div className="h-48 bg-gradient-to-br from-blue-100 via-purple-50 to-pink-100 flex items-center justify-center">
          <div className="text-center">
            <div className="w-16 h-16 bg-white rounded-2xl flex items-center justify-center mx-auto mb-3 shadow-sm">
              <BookOpen className="text-blue-600" size={32} />
            </div>
            <div className="text-sm text-gray-600">{course.faculty || course.category || 'Cours g√©n√©ral'}</div>
          </div>
        </div>
      </div>

      <div className="p-6">
        {/* Titre et description */}
        <div className="mb-4">
          <h3 className="text-lg font-bold text-gray-900 mb-2 line-clamp-2">
            {course.title}
          </h3>
          <p className="text-gray-600 text-sm line-clamp-3 mb-3">
            {course.description}
          </p>
        </div>

        {/* M√©tadonn√©es */}
        <div className="flex items-center gap-4 mb-4 text-sm text-gray-600">
          <div className="flex items-center gap-1">
            <Users size={14} />
            <span>{enrolledStudents.toLocaleString()}</span>
          </div>
          <div className="flex items-center gap-1">
            <Clock size={14} />
            <span>{course.duration || course.estimatedTime || '1h 30min'}</span>
          </div>
          {course.difficulty && (
            <div className={`flex items-center gap-1 px-2 py-1 rounded-full text-xs font-medium ${
              course.difficulty === 'beginner' ? 'bg-green-100 text-green-700' :
              course.difficulty === 'intermediate' ? 'bg-yellow-100 text-yellow-700' :
              'bg-red-100 text-red-700'
            }`}>
              <Zap size={12} />
              {course.difficulty === 'beginner' ? 'D√©butant' : 
               course.difficulty === 'intermediate' ? 'Interm√©diaire' : 'Avanc√©'}
            </div>
          )}
        </div>

        {/* Prix en cr√©dits cognitifs */}
        {!course.isOwned && !isUnlocked && course.creditCost && (
          <div className="flex items-center justify-center gap-2 py-3 px-4 bg-gradient-to-r from-purple-50 to-blue-50 rounded-xl border border-purple-200 mb-4">
            <span className="text-2xl">üß†</span>
            <span className="text-lg font-bold text-gray-900">{course.creditCost}</span>
            <span className="text-sm text-gray-600">cr√©dits</span>
          </div>
        )}

        {/* Badge d√©bloqu√© */}
        {isUnlocked && (
          <div className="flex items-center justify-center gap-2 py-3 px-4 bg-green-50 rounded-xl border border-green-200 mb-4">
            <CheckCircle className="text-green-600" size={16} />
            <span className="text-sm font-medium text-green-700">Cours d√©bloqu√©</span>
          </div>
        )}

        {/* Quiz termin√© */}
        {quizCompleted && quizScore && (
          <div className="flex items-center justify-center gap-2 py-3 px-4 bg-yellow-50 rounded-xl border border-yellow-200 mb-4">
            <Zap className="text-yellow-600" size={16} />
            <span className="text-sm font-medium text-yellow-700">
              Quiz termin√©: {quizScore.score}/{quizScore.total}
            </span>
          </div>
        )}


        {/* Actions - Aper√ßu et Se tester toujours disponibles */}
        <div className="space-y-2">
          <div className="grid grid-cols-2 gap-2">
            {/* Aper√ßu - TOUJOURS disponible */}
            <motion.button
              onClick={(e) => {
                e.stopPropagation();
                console.log('üîç Aper√ßu cliqu√© pour:', course.title);
                onPreview(course.id);
              }}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              className="flex items-center justify-center gap-2 py-3 px-4 bg-gray-100 text-gray-700 rounded-xl font-medium hover:bg-gray-200 transition-colors"
            >
              <Eye size={14} />
              Aper√ßu
            </motion.button>

            {/* Action principale selon l'√©tat du cours */}
            {isUnlocked ? (
              <motion.button
                onClick={(e) => {
                  e.stopPropagation();
                  // onOpenCourse?.(course.id);
                }}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="flex items-center justify-center gap-2 py-3 px-4 bg-gradient-to-r from-green-500 to-green-600 text-white rounded-xl font-medium shadow-lg hover:shadow-xl transition-all duration-200"
              >
                <Play size={14} />
                Commencer
              </motion.button>
            ) : hasMiniQuiz && !quizCompleted ? (
              <motion.button
                onClick={(e) => {
                  e.stopPropagation();
                  setShowMiniQuiz(true);
                }}
                whileHover={{ scale: 1.02 }}
                whileTap={{ scale: 0.98 }}
                className="flex items-center justify-center gap-2 py-3 px-4 bg-gradient-to-r from-purple-500 to-purple-600 text-white rounded-xl font-medium shadow-lg hover:shadow-xl transition-all duration-200"
              >
                <Zap size={14} />
                Se tester
              </motion.button>
            ) : (
              <motion.button
                onClick={(e) => {
                  e.stopPropagation();
                  console.log('üß† D√©blocage cliqu√© pour:', course.title, { canAfford, creditCost: course.creditCost });
                  onUnlock(course.id);
                }}
                whileHover={{ scale: canAfford ? 1.02 : 1 }}
                whileTap={{ scale: canAfford ? 0.98 : 1 }}
                disabled={!canAfford}
                className={`flex items-center justify-center gap-2 py-3 px-4 font-medium rounded-xl transition-all duration-200 ${
                  canAfford 
                    ? 'bg-gradient-to-r from-blue-500 to-purple-600 text-white shadow-lg hover:shadow-xl'
                    : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                }`}
              >
                <Brain size={14} />
                {canAfford 
                  ? `D√©bloquer (${course.creditCost || 1} üß†)` 
                  : 'Cr√©dits insuffisants'
                }
              </motion.button>
            )}
          </div>
          
          {/* Bouton de d√©blocage apr√®s quiz */}
          {quizCompleted && (
            <motion.button
              onClick={(e) => {
                e.stopPropagation();
                onUnlock(course.id);
              }}
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              className="w-full flex items-center justify-center gap-2 py-3 px-4 bg-gradient-to-r from-green-500 to-green-600 text-white rounded-xl font-medium shadow-lg hover:shadow-xl transition-all duration-200"
            >
              <ArrowRight size={16} />
              D√©bloquer maintenant
            </motion.button>
          )}
        </div>
      </div>
    </motion.div>
  );
}
